#fileInput {
	opacity: 0;
	visibility: hidden;
	position: absolute;
}

#dropContainer {
	background: $lightblue;
	max-width: 80%;
	margin: 0 auto;
	border: 1px dashed $black;
	height: 150px;
	box-shadow: 0 0 0 10px $lightblue;
	margin-top: 30px;
	display: flex;
	align-items: center;
	justify-content: center;

	&.darken {
		background: darken($lightblue, 55);
		box-shadow: 0 0 0 10px darken($lightblue, 55)
	}

	&:hover {
		cursor: pointer;
	}
}

.message {
	max-width: 50%;
	margin: 0 auto;
	font-size: 20px;
	text-align: center;
}